import { Component, OnInit } from '@angular/core';
import { DataService } from '../../../../../services/data.service';
import {Observable} from 'rxjs';

@Component({
  selector: 'app-orw-list-loaded',
  templateUrl: './orw-list-loaded.component.html',
  styleUrls: ['./orw-list-loaded.component.css']
})
export class OrwListLoadedComponent implements OnInit {
  loadedorwlist :string [];
   
  userdistrict :string;
  userdistrictid :string;
  usermarp: string;
  userproject: string;

  constructor(private _dataService : DataService) { 

  }

  ngOnInit() {
    this._dataService.currentloginstatus.subscribe(
        data=>{
            if(data.status)
            {
                debugger;
                this._dataService.currentuser.subscribe(
                    data=>{
                        this.userdistrict = data.districtname;
                        this.usermarp = data.marptype;
                        this.userproject = data.projectcode;
                        this.userdistrictid = data.districtid;

                        this.getLoadedORWList();
                    }
                );
        
            }
        }
    ); 
  }

  getLoadedORWList()
  {
    let orw : any = {};
    orw.districtid = this.userdistrictid;
    orw.projectid = this.userproject;
    orw.workflowstatus = 'L';
    
    this._dataService.getORWList(orw).subscribe(
    data=> {
      this.loadedorwlist = data;
    },
    error=>alert(error),
    );
  }
}



