import { Component, OnInit } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { DataService } from '../../../services/data.service';
import { Extent } from '../../../models/extent.interface';
import { IMultiSelectOption, IMultiSelectTexts, IMultiSelectSettings  } from 'angular-2-dropdown-multiselect';
import {Observable} from 'rxjs';
import {MapComponent} from '../map/map.component';
import {
  CanActivate, Router,
  ActivatedRouteSnapshot,
  RouterStateSnapshot
} from '@angular/router';

@Component({
  selector: 'app-side-bar',
  templateUrl: './side-bar.component.html',
  styleUrls: ['./side-bar.component.css']
})
export class SideBarComponent implements OnInit {
  
  isuserdetailsvisible: boolean = true;
  selecteddistricts: string[];
  districtlist: IMultiSelectOption[];
  selecteddsds : string [];
  dsdlist : IMultiSelectOption[];
  extent : Extent;
  orwlist :string [];
  
  userdistrict :string;
  userdistrictid :string;
  usermarp: string;
  userproject: string;

  navigationurl: string = '/';

  multiselectsettings: IMultiSelectSettings = {    
    enableSearch: true,
    checkedStyle: 'checkboxes',
    buttonClasses: 'btn btn-default btn-block',
    dynamicTitleMaxItems: 3,
    displayAllSelectedText: true
  };

  singleselectsettings: IMultiSelectSettings = {    
    enableSearch: true,
    checkedStyle: 'checkboxes',
    buttonClasses: 'btn btn-default btn-block',
    dynamicTitleMaxItems: 3,
    displayAllSelectedText: true,
    selectionLimit: 1,
    autoUnselect: true,
    closeOnSelect: true,
  };
  
  // Text configuration 
  dropdowntexts: IMultiSelectTexts = {
      checkAll: 'Select all',
      uncheckAll: 'Unselect all',
      checked: 'item selected',
      checkedPlural: 'items selected',
      searchPlaceholder: 'Find',
      defaultTitle: 'Select',
      allSelected: 'All selected',
  };
  constructor(private _dataService : DataService, private router: Router, private _mapComponent : MapComponent ) { }

  ngOnInit() {

        this._dataService.currentloginstatus.subscribe(
            data=>{
                if(data.status)
                {
                    this._dataService.currentuser.subscribe(
                        data=>{
                            this.userdistrict = data.districtname;
                            this.usermarp = data.marptype;
                            this.userproject = data.projectcode;
                            this.userdistrictid = data.districtid;
                                    
                            this.getDistrictExtent(this.userdistrictid);
                            this.getDSDsByDistrict(this.userdistrictid);   
                        }
                    );
            
                }
                else{
                    this.navigationurl = '/';
                    this.router.navigate([this.navigationurl]);
                }
            }
        );     
  }

  onDSDChange() {        
        this.getDSDExtent(this.selecteddsds[0]);
  }

  getDSDsByDistrict(districtid : string )
  {
      this._dataService.getDSDList(districtid).subscribe(
          data=> this.dsdlist = data.map((ab: any) => {
                  return { id: ab.id, name: ab.dsd_name };
          }),
          error=>alert(error),
          ()=>console.log(this.districtlist)
      );
  }

  getDSDExtent(dsdid : string)
  {
      this._dataService.getDSDExtent(dsdid).subscribe(
          data=> {
            this._dataService.changeExtent(JSON.stringify(data)); 
          },
          error=>alert(error),
          ()=>console.log(this.extent)
      );
  }

  getDistrictExtent(districtid : string)
  {
      this._dataService.getDistrictExtent(districtid).subscribe(
          data=> 
          {
            this._dataService.changeExtent(JSON.stringify(data)); 
          },
          error=>alert(error),
          ()=>console.log(this.extent)
      );
  }
  
  OnShowUserDetails(){
      this.isuserdetailsvisible = true;
  }

  OnUserdetailsClose(){
      this.isuserdetailsvisible = false;
  }
}



