import { Component, OnInit } from '@angular/core';
import { DataService } from '../../../../../services/data.service';

@Component({
  selector: 'app-orw-list-pending',
  templateUrl: './orw-list-pending.component.html',
  styleUrls: ['./orw-list-pending.component.css']
})
export class OrwListPendingComponent implements OnInit {
  
  pendingorwlist :string [];
  
  userdistrict :string;
  userdistrictid :string;
  usermarp: string;
  userproject: string;
  
  constructor(private _dataService : DataService) { 

  }

  ngOnInit() {
    this._dataService.currentloginstatus.subscribe(
        data=>{
            if(data.status)
            {
                debugger;
                this._dataService.currentuser.subscribe(
                    data=>{
                        this.userdistrict = data.districtname;
                        this.usermarp = data.marptype;
                        this.userproject = data.projectcode;
                        this.userdistrictid = data.districtid;

                        this.getPendingORWList();
                    }
                );
        
            }
        }
    );  
  }

  getPendingORWList()
  {
    let orw : any = {};
    orw.districtid = this.userdistrictid;
    orw.projectid = this.userproject;
    orw.workflowstatus = 'W';
    
    this._dataService.getORWList(orw).subscribe(
    data=> {
      this.pendingorwlist = data;
    },
    error=>alert(error),
    );
  }
}



