import { Component, OnInit, Inject } from '@angular/core';
import { MdDialog, MdDialogRef, MD_DIALOG_DATA } from '@angular/material';
import { DataService } from '../../../services/data.service';
import { IMultiSelectOption, IMultiSelectTexts, IMultiSelectSettings  } from 'angular-2-dropdown-multiselect';

@Component({
  selector: 'app-orw-dialog',
  templateUrl: './orw-dialog.component.html',
  styleUrls: ['./orw-dialog.component.css']
})
export class OrwDialogComponent implements OnInit {

  showorwdetails : boolean = false;
  selectedORWname : string;
  selectedORWdescription : string;
  selectedORW : string[];  
  ORWlist: IMultiSelectOption[];
  selecteddsds : string [];
  dsdlist : IMultiSelectOption[]; 
  orwgeom : string;
  valid :boolean = false;
  //loadedorwlist :string [];
   
  userdistrict :string;
  userdistrictid :string;
  usermarp: string;
  userproject: string;

  multiselectsettings: IMultiSelectSettings = {    
    enableSearch: true,
    checkedStyle: 'checkboxes',
    buttonClasses: 'btn btn-default btn-block',
    dynamicTitleMaxItems: 3,
    displayAllSelectedText: true
  };

  singleselectsettings: IMultiSelectSettings = {    
    enableSearch: true,
    checkedStyle: 'checkboxes',
    buttonClasses: 'btn btn-default btn-block',
    dynamicTitleMaxItems: 3,
    displayAllSelectedText: true,
    selectionLimit: 1,
    autoUnselect: true,
    closeOnSelect: true,
  };
  
  // Text configuration 
  dropdowntexts: IMultiSelectTexts = {
      checkAll: 'Select all',
      uncheckAll: 'Unselect all',
      checked: 'item selected',
      checkedPlural: 'items selected',
      searchPlaceholder: 'Find',
      defaultTitle: 'Select',
      allSelected: 'All selected',
  };

  constructor(
    private _dataService : DataService,
    public dialogRef: MdDialogRef<OrwDialogComponent>,
    @Inject(MD_DIALOG_DATA) public data: any) { }

  cancel(): void {
    this.dialogRef.close();
  }

  ngOnInit() {
    this._dataService.currentloginstatus.subscribe(
        data=>{
            if(data.status)
            {
                debugger;
                this._dataService.currentuser.subscribe(
                    data=>{
                        this.userdistrict = data.districtname;
                        this.usermarp = data.marptype;
                        this.userproject = data.projectcode;
                        this.userdistrictid = data.districtid;

                        this.getLoadedORWList();
                        this.getDSDsByDistrict();
                    }
                );
        
            }
        }
    ); 

    this._dataService.currentgeom.subscribe(data => 
    {      
      if(data != '')
      {
        debugger;
          this.orwgeom = data;
          this.valid = true;
      }
    });

  }

  getDSDsByDistrict()
  {
    this._dataService.getDSDList(this.userdistrictid).subscribe(
        data=> this.dsdlist = data.map((ab: any) => {
                return { id: ab.id, name: ab.dsd_name };
        }),
        error=>alert(error),
        ()=>console.log(this.dsdlist)
    );
  }

  getLoadedORWList()
  {
    let orw : any = {};
    orw.districtid = this.userdistrictid;
    orw.projectid = this.userproject;
    orw.workflowstatus = 'L';
    
    this._dataService.getORWList(orw).subscribe(
    data=> this.ORWlist = data.map((ab: any) => {
                return { id: ab.id, name: ab.area_name };
    }),   
    error=>alert(error),
    );
  } 

  onORWChange()
  {
    this.showorwdetails = true;

    this._dataService.getORW(this.selectedORW[0]).subscribe(
          data=> 
          {
            this.selectedORWdescription = data[0].description,
            this.selectedORWname = data[0].area_name
          },
          error=>alert(error)
    );
  } 

  addORW()
  {
    if(this.selectedORW != undefined  && this.selectedORW.length>0)
    {
        let orw : any = {};
        orw.geom = this.orwgeom;
        orw.dsdid = this.selecteddsds[0];
        orw.id = this.selectedORW[0];

        this._dataService.updateORW(orw).subscribe(
        data=> {
          debugger;
          this.dialogRef.close();
        },
        error=>alert(error),
        );
    }
    else{
      alert('pls select orw');
    }
  } 
}



