import { Component, OnInit } from '@angular/core';
import { DataService } from '../../../../../services/data.service';
import {Observable} from 'rxjs';
import { User } from '../../../../../models/user.interface';

@Component({
  selector: 'app-orw-list-published',
  templateUrl: './orw-list-published.component.html',
  styleUrls: ['./orw-list-published.component.css']
})
export class OrwListPublishedComponent implements OnInit {

  publishedorwlist :string [];
   
  userdistrict :string;
  userdistrictid :string;
  usermarp: string;
  userproject: string;
  temp: any;

  constructor(private _dataService : DataService) { 

  }

  ngOnInit() {
    this._dataService.currentloginstatus.subscribe(
        data=>{
            if(data.status)
            {
                debugger;
                this._dataService.currentuser.subscribe(
                    data=>{
                        this.userdistrict = data.districtname;
                        this.usermarp = data.marptype;
                        this.userproject = data.projectcode;
                        this.userdistrictid = data.districtid;

                        this.getPublishedORWList();
                    }
                );
        
            }
        }
    ); 

    this._dataService.currentorw.subscribe(
        data => {
            debugger;
            this.temp = {id: data};
        }
    ); 
  }

  GetORWDetails(orw : any)
  {
      this.temp = orw;
      this._dataService.ChangeORW(orw.id);
  }

  getPublishedORWList()
  {      
    let orw : any = {};
    orw.districtid = this.userdistrictid;
    orw.projectid = this.userproject;
    orw.workflowstatus = 'P';
    
    this._dataService.getORWList(orw).subscribe(
    data=> {
        debugger;
      this.publishedorwlist = data;
    },
    error=>alert(error),
    );
  }
}



