import { Component, OnInit } from '@angular/core';
import { LocalStorageService } from 'angular-2-local-storage';

import {
  CanActivate, Router,
  ActivatedRouteSnapshot,
  RouterStateSnapshot
} from '@angular/router';
import { User } from '../../../models/user.interface';
import { LoginStatus } from '../../../models/loginstatus.interface';
import { DataService } from '../../../services/data.service';
import {Observable} from 'rxjs';

@Component({
  selector: 'app-sign-in',
  templateUrl: './sign-in.component.html',
  styleUrls: ['./sign-in.component.css']
})
export class SignInComponent implements OnInit {  
  
  msg : string = '';
  naviationurl: string = 'pe-mapper';
  currentuser : User;
  user : any = {};
  loginstatus : LoginStatus;
  loginerror : string;

  constructor(private _dataService : DataService, private router: Router, private localStorageService : LocalStorageService ) {
    this.currentuser = this._dataService.GetCurrentUser();  
   }

  ngOnInit() {
    this._dataService.currentloginstatus.subscribe(data => 
    {      
      debugger;
        this.loginstatus = data;

        if(this.loginstatus.status)
        {
          this.naviationurl = 'pe-mapper/orw/p/details';
          this.router.navigate([this.naviationurl]);
        }
        else if(!this.loginstatus.status){
          this.naviationurl = '/';
          this.router.navigate([this.naviationurl]);
        }
    });
  }


  login(user : User )
  {
        this._dataService.Authenticate(user);
  }

  logout(){
    debugger;
    this._dataService.Logout();
    this.currentuser = this._dataService.GetCurrentUser();      
  }

}



