import { Component, OnInit, Inject } from '@angular/core';
import { MdDialog, MdDialogRef, MD_DIALOG_DATA } from '@angular/material';
import { DataService } from '../../../services/data.service';
import { IMultiSelectOption, IMultiSelectTexts, IMultiSelectSettings  } from 'angular-2-dropdown-multiselect';

@Component({
  selector: 'app-hotspot-dialog',
  templateUrl: './hotspot-dialog.component.html',
  styleUrls: ['./hotspot-dialog.component.css']
})
export class HotspotDialogComponent implements OnInit {
  showhotspotdetails : boolean = false;
  selectedORWname : string;
  selectedhotspotname : string;
  marpgroupname : string;

  selectedORW : string[];  
  selectedORWid : string; 

  selectedhotspots : string [];
  hostspotlist : IMultiSelectOption[]; 

  selectedHotspotType: string[];  
  hotspottypelist: IMultiSelectOption[];

  hotspotgeom : string;
  valid :boolean = false;

  multiselectsettings: IMultiSelectSettings = {    
    enableSearch: true,
    checkedStyle: 'checkboxes',
    buttonClasses: 'btn btn-default btn-block',
    dynamicTitleMaxItems: 3,
    displayAllSelectedText: true
  };

  singleselectsettings: IMultiSelectSettings = {    
    enableSearch: true,
    checkedStyle: 'checkboxes',
    buttonClasses: 'btn btn-default btn-block',
    dynamicTitleMaxItems: 3,
    displayAllSelectedText: true,
    selectionLimit: 1,
    autoUnselect: true,
    closeOnSelect: true,
  };
  
  // Text configuration 
  dropdowntexts: IMultiSelectTexts = {
      checkAll: 'Select all',
      uncheckAll: 'Unselect all',
      checked: 'item selected',
      checkedPlural: 'items selected',
      searchPlaceholder: 'Find',
      defaultTitle: 'Select',
      allSelected: 'All selected',
  };

  constructor(private _dataService : DataService,
    public dialogRef: MdDialogRef<HotspotDialogComponent>,
    @Inject(MD_DIALOG_DATA) public data: any) { }

  ngOnInit() {
      this._dataService.getHotspotList('23').subscribe(
          data=> this.hostspotlist = data.map((ab: any) => {
                  return { id: ab.id, name: ab.hotspot_name };
          }),
          error=>alert(error),
          ()=>console.log(this.hostspotlist)
      );

      this._dataService.getHotspotType().subscribe(
          data=> this.hotspottypelist = data.map((ab: any) => {
                  return { id: ab.id, name: ab.name };
          }),
          error=>alert(error),
          ()=>console.log(this.hotspottypelist)
      );


      this._dataService.currentgeom.subscribe(data => 
      {      
        if(data != '')
        {
          debugger;
            this.hotspotgeom = data;
            this.valid = true;
        }
      });

      this._dataService.currentorw.subscribe(
       data=>{
          this.selectedORWid = data;
          this._dataService.getORW(data).subscribe(
          data=> {
              if(data != null){
                  debugger;
                  this.selectedORW = data[0];      
              }            
          },
          error=>{
              debugger;
          });
       }
     );
  }

  addHotspot()
  {
    debugger;
    let hotspot : any = {};
    hotspot.geom = this.hotspotgeom;
    hotspot.id = this.selectedhotspots[0];
    hotspot.type = this.selectedHotspotType[0];
    hotspot.orwid = this.selectedORWid;

    this._dataService.updateHotspot(hotspot).subscribe(
    data=> {
      debugger;
    },
    error=>alert(error),
    );
  } 

  cancel(): void {
    this.dialogRef.close();
  }

  onHotspotChange()
  {
    this.showhotspotdetails = true;

    this._dataService.getHotspot(this.selectedhotspots[0]).subscribe(
          data=> 
          {
            this.selectedhotspotname = data[0].hotspot_name,
            this.marpgroupname = data[0].marp_group_name
          },
          error=>alert(error)
    );    
  }

  onHotspotTypeChange()
  {

  }

}



