import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule} from '@angular/router';
import { HttpModule} from '@angular/http';
import { FormsModule } from '@angular/forms';
import { MultiselectDropdownModule } from 'angular-2-dropdown-multiselect';
import { LocalStorageModule } from 'angular-2-local-storage';
import { LeafletModule } from '@asymmetrik/ngx-leaflet';
import { LeafletDrawModule } from '@asymmetrik/ngx-leaflet-draw';
import {MdDialogModule} from '@angular/material';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';



import { AppComponent } from './app.component';
import { NavBarComponent } from './components/nav-bar/nav-bar.component';
import { LoginComponent } from './components/login/login.component';
import { SignInComponent } from './components/login/sign-in/sign-in.component';

import { DataService } from './services/data.service';

import { PeMapperComponent } from './components/pe-mapper/pe-mapper.component';
import { SideBarComponent } from './components/pe-mapper/side-bar/side-bar.component';
import { MapComponent } from './components/pe-mapper/map/map.component';
import { AdminComponent } from './components/admin/admin.component';
import { LeftPanelComponent } from './components/admin/left-panel/left-panel.component';
import { ContentComponent } from './components/admin/content/content.component';
import { OrwDialogComponent } from './components/pe-mapper/orw-dialog/orw-dialog.component';
import { HotspotDialogComponent } from './components/pe-mapper/hotspot-dialog/hotspot-dialog.component';
import { OrwListPublishedComponent } from './components/pe-mapper/side-bar/orw-list/orw-list-published/orw-list-published.component';
import { OrwListLoadedComponent } from './components/pe-mapper/side-bar/orw-list/orw-list-loaded/orw-list-loaded.component';
import { OrwListComponent } from './components/pe-mapper/side-bar/orw-list/orw-list.component';
import { OrwDetailsComponent } from './components/pe-mapper/side-bar/orw-details/orw-details.component';
import { OrwListPendingComponent } from './components/pe-mapper/side-bar/orw-list/orw-list-pending/orw-list-pending.component';
import { OrwHotspotListComponent } from './components/pe-mapper/side-bar/orw-details/orw-hotspot-list/orw-hotspot-list.component';
import { GeneralDetailsComponent } from './components/pe-mapper/side-bar/orw-details/general-details/general-details.component';
import { ManageUserComponent } from './components/admin/manage-user/manage-user.component';

@NgModule({
  declarations: [
    AppComponent,
    NavBarComponent,
    LoginComponent,
    SignInComponent,
    PeMapperComponent,
    SideBarComponent,
    MapComponent,
    AdminComponent,
    LeftPanelComponent,
    ContentComponent,
    OrwDialogComponent,
    HotspotDialogComponent,
    OrwListPublishedComponent,
    OrwListLoadedComponent,
    OrwListComponent,
    OrwDetailsComponent,
    OrwListPendingComponent,
    OrwHotspotListComponent,
    GeneralDetailsComponent,
    ManageUserComponent
    
  ],
  entryComponents: [
    OrwDialogComponent,
    HotspotDialogComponent
  ],
  imports: [
    BrowserModule,
    MdDialogModule,
    BrowserAnimationsModule,
    FormsModule,
    MultiselectDropdownModule,
    LocalStorageModule.withConfig({
            prefix: 'app-root',
            storageType: 'localStorage'
        }),
    LeafletModule.forRoot(),
    LeafletDrawModule.forRoot(),
    RouterModule.forRoot([
      {
        path:'',
        component:LoginComponent
        //component:IndexComponent
      },
      {
        path:'login',
        component:LoginComponent
      },
      {
        path:'pe-mapper',
        component:PeMapperComponent,
        children:[
        {
          path:'orw/l',
          component:OrwListLoadedComponent
        },
        {
          path:'orw/p',
          component:OrwListPublishedComponent,
          children:[
          {
            path:'details',
            component:GeneralDetailsComponent
          },
          {
            path:'hotspots',
            component:OrwHotspotListComponent
          }]
        },
        {
          path:'orw/w',
          component:OrwListPendingComponent
        }]
      },
      {
        path:'admin',
        component:AdminComponent,
        children:[
          {
            path:'manage-user',
            component:ManageUserComponent
          },
          {
            path:'hotspots',
            component:OrwHotspotListComponent
          },
          {
            path:'orw',
            component:OrwHotspotListComponent
          }]
      }       
    ]),
    HttpModule
  ],
  providers: [DataService, MapComponent],
  bootstrap: [AppComponent]
})
export class AppModule { }



