import { Component, OnInit } from '@angular/core';
import { DataService } from '../../services/data.service';
import { LoginStatus } from '../../models/loginstatus.interface';
import { User } from '../../models/user.interface';

@Component({
  selector: 'app-nav-bar',
  templateUrl: './nav-bar.component.html',
  styleUrls: ['./nav-bar.component.css']
})
export class NavBarComponent implements OnInit {

  loginstatus : LoginStatus;
  user : User = null;

  constructor(private _dataService : DataService) {

   }

  ngOnInit() {
    //this.loginstatus = {message:"not logged in",status:false};
    this._dataService.currentloginstatus.subscribe(data => 
    {    
        debugger;  
        this.loginstatus = data;
    });

    this._dataService.currentuser.subscribe(data=>
    {
      this.user = data;
    });
  }

  OnSignout()
  {
    debugger;
    let _status : LoginStatus = {status:false, message:"Not signed in"};
    this._dataService.ChangeLoginStatus(_status);
  }

}



