import { Component, OnInit } from '@angular/core';
import { DataService } from '../../../services/data.service';
import { IMultiSelectOption, IMultiSelectTexts, IMultiSelectSettings  } from 'angular-2-dropdown-multiselect';


@Component({
  selector: 'app-manage-user',
  templateUrl: './manage-user.component.html',
  styleUrls: ['./manage-user.component.css']
})
export class ManageUserComponent implements OnInit {
  isnotuserselected :boolean =true;
  isnotdistrictselected :boolean =true;
  isnotmarpselected :boolean =true;
  isnotprojectselected :boolean =true;

  selectedusers : string[];  
  userlist: IMultiSelectOption[];

  selecteddistricts : string[];  
  distrcitlist: IMultiSelectOption[];

  selectedmarpgroups : string[];  
  marpgrouplist: IMultiSelectOption[];

  selectedprojects : string[];  
  projectlist: IMultiSelectOption[];

  multiselectsettings: IMultiSelectSettings = {    
    enableSearch: true,
    checkedStyle: 'checkboxes',
    buttonClasses: 'btn btn-default btn-block',
    dynamicTitleMaxItems: 3,
    displayAllSelectedText: true
  };

  singleselectsettings: IMultiSelectSettings = {    
    enableSearch: true,
    checkedStyle: 'checkboxes',
    buttonClasses: 'btn btn-default btn-block',
    dynamicTitleMaxItems: 3,
    displayAllSelectedText: true,
    selectionLimit: 1,
    autoUnselect: true,
    closeOnSelect: true,
  };
  
  // Text configuration 
  dropdowntexts: IMultiSelectTexts = {
      checkAll: 'Select all',
      uncheckAll: 'Unselect all',
      checked: 'item selected',
      checkedPlural: 'items selected',
      searchPlaceholder: 'Find',
      defaultTitle: 'Select',
      allSelected: 'All selected',
  };

  constructor(private _dataService : DataService) { }

  ngOnInit() {
      this._dataService.getDistrictList().subscribe(
          data=> this.distrcitlist = data.map((ab: any) => {
                  return { id: ab.id, name: ab.district_name };
          }),
          error=>alert(error),
          ()=>console.log(this.distrcitlist)
      );

      this._dataService.getUserList().subscribe(
          data=> this.userlist = data.map((ab: any) => {
                  return { id: ab.id, name: ab.fullname };
          }),
          error=>alert(error),
          ()=>console.log(this.userlist)
      );

      this._dataService.getMarpGroupList().subscribe(
          data=> this.marpgrouplist = data.map((ab: any) => {
                  return { id: ab.marp_group_id, name: ab.marp_group_id };
          }),
          error=>alert(error),
          ()=>console.log(this.marpgrouplist)
      );

      this._dataService.getProjectList().subscribe(
          data=> this.projectlist = data.map((ab: any) => {
                  return { id: ab.project_code, name: ab.project_name };
          }),
          error=>alert(error),
          ()=>console.log(this.marpgrouplist)
      );


  }

  onUserChange()
  {    
    if(this.selectedusers.length > 0)
    {
      this.isnotuserselected = false;
    }
    else{
      this.isnotuserselected = true;
    }    
  }

  onDistrictChange()
  {
    if(this.selecteddistricts.length > 0)
    {
      this.isnotdistrictselected = false;
    }
    else{
      this.isnotdistrictselected = true;
    }    
  }

  onMARPGroupChange()
  {
    if(this.selectedmarpgroups.length > 0)
    {
      this.isnotmarpselected = false;
    }
    else{
      this.isnotmarpselected = true;
    }      
  }

  onProjectChange()
  {
     if(this.selectedprojects.length > 0)
    {
      this.isnotprojectselected = false;
    }
    else{
      this.isnotprojectselected = true;
    }     
  }

  updateUser()
  {
      let user : any = {};

      user.id = this.selectedusers[0];
      user.districtid = this.selecteddistricts[0];
      user.marpgroup = this.selectedmarpgroups[0];
      user.projectid = this.selectedprojects[0];

      this._dataService.updateUser(user).subscribe(
      data=> {
        debugger;
        this.selecteddistricts = [];
        this.selectedusers = [];
        this.selectedmarpgroups = [];
        this.selectedprojects = [];
      },
      error=>alert(error),
      );   
  }
}



