import { Injectable } from '@angular/core';
import { Http, Response, RequestOptions, Headers } from '@angular/http';
import {Observable} from 'rxjs';
import 'rxjs/add/operator/map';
import { User } from '../models/user.interface';
import { LoginStatus } from '../models/loginstatus.interface';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';


@Injectable()
export class DataService {
  baseurl :string = 'http://waterdata.iwmi.org/pe';

  private extent = new BehaviorSubject('');
  currentextent = this.extent.asObservable();

  private geom = new BehaviorSubject('');
  currentgeom = this.geom.asObservable();


  private user : BehaviorSubject <User> = new BehaviorSubject (null);
  currentuser = this.user.asObservable();

  private loginstatus : BehaviorSubject <LoginStatus> = new BehaviorSubject ({message:"",status:false});
  currentloginstatus = this.loginstatus.asObservable();

  private orw = new BehaviorSubject('');
  currentorw = this.orw.asObservable();


//loginstatus : LoginStatus = {message:"not logged in",status:false};


  constructor(private _http : Http) { }

    //USER LOGIN
    AuthenticateUser(user : User): Observable<any> {
        let url =  this.baseurl + '/services/index.php/login';
        let headers = new Headers({ 'Content-Type': 'application/json' });
        
        let options = new RequestOptions({ headers: headers });
        return this._http.post(url, user ,headers)
                    .map(res=>{
                    debugger;
                    if(res.json().length != 0)
                    {
                        localStorage.setItem('currentuser', JSON.stringify(res.json()));
                        return res.json();
                    }
                    else{
                        return null;
                    }                  
                    });
    } 

    GetCurrentUser(){
        return JSON.parse(localStorage.getItem('currentuser'));
    }

    Logout(){
        return localStorage.removeItem('currentuser');
    }



    getDistrictList()
    {
        return this._http.get(this.baseurl + '/services/index.php/districts/get')
        .map(response => response.json());   
    }

    getUserList()
    {
        return this._http.get(this.baseurl + '/services/index.php/users/get')
        .map(response => response.json());         
    }

    getMarpGroupList()
    {
        return this._http.get(this.baseurl + '/services/index.php/marpgroup/get')
        .map(response => response.json());          
    }

    getProjectList()
    {
        return this._http.get(this.baseurl + '/services/index.php/projects/get')
        .map(response => response.json());          
    }

    getDSDList(districtid : string)
    {
        return this._http.get(this.baseurl + '/services/index.php/dsds/get/' + districtid)
        .map(response => response.json());   
    }

    getDSDExtent(dsdtid : string)
    {
        return this._http.get(this.baseurl + '/services/index.php/district/extent/' + dsdtid)
        .map(response => response.json());   
    }

    getDistrictExtent(districtid : string)
    {
        return this._http.get(this.baseurl + '/services/index.php/dsd/extent/' + districtid)
        .map(response => response.json());   
    }

    changeExtent(_extent: string) {        
        this.extent.next(_extent);
    }

    changeGeom(_geom: string) {        
        this.geom.next(_geom);
    }

    getORWList(orw:any): Observable<any> 
    {       
        let url = this.baseurl + '/services/index.php/orwlist/get';
        let headers = new Headers({ 'Content-Type': 'application/json' });
        
        let options = new RequestOptions({ headers: headers });
        return this._http.post(url, orw,headers)
                .map(res=>res.json());
    } 

    getPublishedORWList(districtid : string)
    {
        return this._http.get(this.baseurl + '/services/index.php/orw/getpublishedlist/' + districtid)
        .map(response => response.json());   
    }

    getPublishedHostpotsList(orwid : string)
    {
        return this._http.get(this.baseurl + '/services/index.php/hotspots/publishedlist/' + orwid)
        .map(response => response.json());   
    }

    getORW(orwid : string)
    {
        return this._http.get(this.baseurl + '/services/index.php/orw/get/' + orwid)
        .map(response => response.json());   
    }
    
    getHotspotList(districtid : string)
    {
        return this._http.get(this.baseurl + '/services/index.php/hotspots/get/' + districtid)
        .map(response => response.json());   
    }

    getHotspot(hotspotid : string)
    {
        return this._http.get(this.baseurl + '/services/index.php/hotspot/get/' + hotspotid)
        .map(response => response.json());   
    }

    getHotspotType()
    {
        return this._http.get(this.baseurl + '/services/index.php/hotspottypes/get')
        .map(response => response.json());   
    }
    
    updateORW(orw:any): Observable<any> {
        let url = this.baseurl + '/services/index.php/orw/update';
        let headers = new Headers({ 'Content-Type': 'application/json' });
        
        let options = new RequestOptions({ headers: headers });
        return this._http.post(url, orw,headers)
                .map(res=>res.json());
    } 

    updateUser(user:any) : Observable<any> {
        let url = this.baseurl + '/services/index.php/user/update';
        let headers = new Headers({ 'Content-Type': 'application/json' });
        
        let options = new RequestOptions({ headers: headers });
        return this._http.post(url, user,headers)
                .map(res=>res.json());
    } 

    updateHotspot(hotspot:any): Observable<any> {
        let url = this.baseurl + '/services/index.php/hotspot/update';
        let headers = new Headers({ 'Content-Type': 'application/json' });
        
        let options = new RequestOptions({ headers: headers });
        return this._http.post(url, hotspot,headers)
                .map(res=>res.json());
    } 

    getoperational_area_geom(districtid : string)
    {
        return this._http.get(this.baseurl + '/services/index.php/operational_area_geom/' + districtid)
        .map(response => response.json());
    }

    Authenticate(user : User)
    {
        debugger;
        let url = this.baseurl + '/services/index.php/user/authenticate';
        let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8' });
        let options = new RequestOptions({ headers: headers });

        this._http.post(url, user, headers)
        .map(response=>response.json())
        .subscribe(
            response => {                                
                let loginresponse = JSON.parse(response)
                if(loginresponse.hasOwnProperty('token'))
                {
                    //get user profile.
                    let url = this.baseurl + '/services/index.php/user/get';
                    let headers = new Headers({ 'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8' });
                    let options = new RequestOptions({ headers: headers });

                    this._http.post(url, user, headers)
                    .map(response=>response.json())
                    .subscribe(response => {
                        debugger;
                        let u : User = 
                        {
                            districtid:response[0].district_id,
                            firstname:response[0].fname,
                            lastname:response[0].lname,
                            marptype:response[0].marp_group,
                            password:null,
                            userid:response[0].id,
                            username:response[0].userId,
                            projectcode:response[0].project_code,
                            districtname:response[0].district_name
                        }

                        this.ChangeUser(u);
                        this.ChangeLoginStatus({status:true,message:"Loggin Success.."});
                    })
                }
                else
                {
                    //show error 
                    this.ChangeLoginStatus({status:false,message:loginresponse.message});
                }
        });
    }

    ChangeUser(_user: User) {        
        this.user.next(_user);
    }

    ChangeLoginStatus(_status : LoginStatus)
    {
        this.loginstatus.next(_status);
    }

    ChangeORW(_orw: string) {        
        this.orw.next(_orw);
    }
}


